export type Nutrition = {
    ml?: number
    g?: number
    count?: number
    calories: number
    fat?: number
    carbs?: number
    minus_carbs?: number
    protein?: number
}

export function plus(...nutritions: Nutrition[]): Nutrition {
    return nutritions.reduce((a, b) => ({
        calories: a.calories + b.calories,
        g: (typeof a.g === "number" && typeof b.g === "number") ? (a.g + b.g ) : undefined,
        ml: (typeof a.ml === "number" && typeof b.ml === "number") ? (a.ml + b.ml ) : undefined,
        fat: (a.fat ?? 0) + (b.fat ?? 0),
        carbs: (a.carbs ?? 0) + (b.carbs ?? 0),
        minus_carbs: (a.minus_carbs ?? 0) + (b.minus_carbs ?? 0),
        protein: (a.protein ?? 0) + (b.protein ?? 0),
    }), {calories: 0, g: 0, ml: 0, protein: 0, carbs: 0, minus_carbs: 0, fat: 0, count: 0});
}

export function times(nutrition: Nutrition, scalar: number): Nutrition {
    return {
        ...nutrition,
        calories: nutrition.calories * scalar,
        g: typeof nutrition.g === "number" ? nutrition.g * scalar :undefined,
        ml: typeof nutrition.ml === "number" ? nutrition.ml * scalar :undefined,
        fat: typeof nutrition.fat === "number" ? nutrition.fat * scalar :undefined,
        carbs: typeof nutrition.carbs === "number" ? nutrition.carbs * scalar :undefined,
        minus_carbs: typeof nutrition.minus_carbs === "number" ? nutrition.minus_carbs * scalar :undefined,
        protein: typeof nutrition.protein === "number" ? nutrition.protein * scalar : undefined,
    };
}

export function gOf(g: number, nutrition: Nutrition): Nutrition {
    return times(nutrition, g / nutrition.g!!);
}

export function mlOf(ml: number, nutrition: Nutrition): Nutrition {
    return times(nutrition, ml / nutrition.ml!!);
}
