import React, {useState} from 'react';
import logo from './logo.svg';
import './App.css';
import * as Facts from "./Facts";
import {Nutrition, plus, times} from "./Nutrition";
import {Food} from "./Food";

const foods: Food[] = Object.values(Facts);

const MALE = 5;
const FEMALE = -161;

enum Sex {
    MALE = 5,
    FEMALE = -161,
}

enum Active {
    Sedentary = 1.2, // Sedentary lifestyle (little or no exercise): 1.2
    Slightly = 1.4, // Slightly active lifestyle (light exercise or sports 1-2 days/week): 1.4
    Moderately = 1.6, // Moderately active lifestyle (moderate exercise or sports 2-3 days/week): 1.6
    Very = 1.75, // Very active lifestyle (hard exercise or sports 4-5 days/week): 1.75
    Extra = 2, // Extra active lifestyle (very hard exercise, physical job, or sports 6-7 days/week): 2
    Professional = 2.3, // Professional athlete: 2.3
}

function BMR(sex: Sex, weight: number, height: number, age: number): number {
    return 10 * weight * 0.4535924 + 6.25 * height * 2.54 - 5 * age + sex;
    // BMR(men)   = (10 × weight / 1 kg + 6.25 × height / 1cm - 5 × age / 1 year + 5) kcal / day
    // BMR(women) = (10 × weight / 1 kg + 6.25 × height / 1cm - 5 × age / 1 year - 161) kcal / day
}

function sustain_calories(sex: Sex, weight: number, height: number, age: number, active: Active): number {
    return BMR(sex, weight, height, age) * active;
}

function protein_range(calories: number): [number, number] {
    const c = calories / 4;
    return [c * 0.1, c * 0.35];
}

function carbs_range(calories: number): [number, number] {
    const c = calories / 4;
    return [c * 0.45, c * 0.65];
}

function fats_range(calories: number): [number, number] {
    const c = calories / 9;
    return [c * 0.2, c * 0.35];
}

type DietDay = {
    date: string
    profile: {
        sex: Sex
        height: number
        weight: number
        active: Active
    }
    target: {
        calories: [number, number],
        fat: [number, number],
        carbs: [number, number],
        protein: [number, number],
    }
    consumed: {
        total: {
            calories: number
            fat: number
            carbs: number
            protein: number
        }
        items: Food[]
    }
}

type Consumption = {
    timestamp: number
    portion: {
        servings: number
        count?: number
        g?: number
        ml?: number
    }
    food: Food
}

interface DietRepository {
    getFoods(): Promise<Food[]>

    putFood(food: Food): Promise<number>

    getConsumptions(): Promise<Food[]>
}

class LocalStorageDietRepository {
    current() {
    }
}

const diet_repo = new LocalStorageDietRepository();


function App() {
    const [weight, setWeight] = useState(parseInt(localStorage.getItem('weight') ?? '222'));
    const [height, setHeight] = useState(parseInt(localStorage.getItem('height') ?? '69'));
    const [age, setAge] = useState(parseInt(localStorage.getItem('age') ?? '40'));
    const bmr = BMR(Sex.MALE, weight, height, age)
    const sustain_calories = bmr * Active.Sedentary;

    const [meals, setMeals] = useState<Food[]>(JSON.parse(localStorage.getItem('nutritions') ?? '[]'));
    const nutrition = plus(...meals);

    const [min_fats, max_fats] = fats_range(sustain_calories);
    const [min_carbs, max_carbs] = carbs_range(sustain_calories);
    const [min_protein, max_protein] = protein_range(sustain_calories);

    function add(nutrition: Food) {
        const updated = [...meals, nutrition];
        localStorage.setItem('nutritions', JSON.stringify(updated));
        setMeals(updated)
    }

    function addCount(nutrition: Food) {
        const serving: number = (nutrition.count ?? 1);
        const portion = prompt(`portion`, serving.toString());

        if (portion === null) {
            return;
        }

        // eslint-disable-next-line no-eval
        const scalar = parseFloat(eval(portion)) / serving;
        add({name: nutrition.name, ...times(nutrition, scalar)});
    }

    function addGrams(nutrition: Food) {
        const serving: number = nutrition.g!!;

        if (!serving) {
            return;
        }


        const portion = prompt(`grams`, serving.toString());

        if (portion === null) {
            return;
        }

        // eslint-disable-next-line no-eval
        const scalar = parseFloat(eval(portion)) / serving;
        add({name: nutrition.name, ...times(nutrition, scalar)});
    }

    function addMilliliters(nutrition: Food) {
        const serving: number = nutrition.ml!!;

        if (!serving) {
            return;
        }

        const portion = prompt(`milliliters`, serving.toString());

        if (portion === null) {
            return;
        }

        // eslint-disable-next-line no-eval
        const scalar = parseFloat(eval(portion)) / serving;
        add({name: nutrition.name, ...times(nutrition, scalar)});
    }

    function remove(i: number) {
        const updated = [...meals];
        updated.splice(i, 1);
        localStorage.setItem('nutritions', JSON.stringify(updated));
        setMeals(updated)
    }

    function netCarbs(nutrition: Nutrition): number | undefined {
        if (nutrition.carbs === undefined) {
            return undefined;
        }

        return nutrition.carbs - (nutrition.minus_carbs ?? 0);
    }

    function reset() {
        localStorage.setItem('nutritions', '[]');
        setMeals([]);
    }

    function nextDay() {
        const date = new Date();
        // const day: DietDay = {
        //     date: `${date.getUTCFullYear()}-${date.getUTCMonth()}-${date.getUTCDate()}`,
        //     profile: {
        //
        //     }
        // }
    }

    function formatNumber(value?: number, unit?: string): string {
        if (typeof value !== "number") {
            return "-";
        }

        return Math.round(value).toString() + (unit ? ` ${unit}` : '');
    }

    function updateWeight(){
        const value = prompt(`weight`, weight.toString());

        if (value === null) {
            return;
        }

        // eslint-disable-next-line no-eval
        const newWeight = parseFloat(eval(value));
        localStorage.setItem('weight', JSON.stringify(newWeight));
        setWeight(newWeight);
    }

    return (
        <div className="App">
            <div>
                <button onClick={()=>updateWeight()}>weight {formatNumber(weight)} lbs</button>
                {/*<div>bmr {formatNumber(bmr)} kcals</div>*/}
                {/*<div>sustain {formatNumber(sustain_calories)} kcals</div>*/}
                {/*<div>target {formatNumber(target)} kcals</div>*/}
            </div>

            {/*<div>*/}
            {/*    <meter min={0}*/}
            {/*           max={sustain_calories}*/}
            {/*           low={sustain_calories - 1000}*/}
            {/*           high={bmr}*/}
            {/*           optimum={sustain_calories-500}*/}
            {/*           value={nutrition.calories}/>*/}
            {/*</div>*/}

            <div>
                <div><meter max={sustain_calories - 1000} value={nutrition.calories}/> {formatNumber(sustain_calories - 1000)} -1 kg/wk</div>
                <div><meter min={sustain_calories - 1000} max={sustain_calories - 500} value={nutrition.calories}/> {formatNumber(sustain_calories - 500)} -1/2 kg/wk</div>
                <div><meter min={sustain_calories - 500} max={bmr} value={nutrition.calories}/> {formatNumber(bmr)} BMR</div>
                <div><meter min={bmr} max={sustain_calories} value={nutrition.calories}/> {formatNumber(sustain_calories)} sustain sedentary</div>
                <div><meter min={sustain_calories} max={bmr * Active.Slightly} value={nutrition.calories}/> {formatNumber(bmr * Active.Slightly)} sustain slightly</div>
                {/*<div><meter min={bmr * Active.Slightly} max={bmr * Active.Moderately} value={nutrition.calories}/> {formatNumber(bmr * Active.Moderately)} sustain moderately</div>*/}
                {/*<div><meter min={bmr * Active.Moderately} max={bmr * Active.Very} value={nutrition.calories}/> {formatNumber(bmr * Active.Very)} sustain very</div>*/}
                {/*<div><meter min={bmr * Active.Very} max={bmr * Active.Extra} value={nutrition.calories}/> {formatNumber(bmr * Active.Extra)} sustain extra</div>*/}
                {/*<div><meter min={bmr * Active.Extra} max={bmr * Active.Professional} value={nutrition.calories}/> {formatNumber(bmr * Active.Professional)} sustain professional</div>*/}
            </div>
            <div>
                {formatNumber(nutrition.calories)} kcal
            </div>

            <div>
                <meter max={min_fats} value={nutrition.fat}/>
                {formatNumber(min_fats)}
                <meter min={min_fats} max={max_fats} value={nutrition.fat}/>
                {formatNumber(max_fats)}
            </div>
            <div>{formatNumber(nutrition.fat)}g fat</div>

            <div>
                <meter max={min_carbs} value={nutrition.carbs}/>
                {formatNumber(min_carbs)}
                <meter min={min_carbs} max={max_carbs} value={nutrition.carbs}/>
                {formatNumber(max_carbs)}
            </div>
            <div>{formatNumber(nutrition.carbs)} ({formatNumber(netCarbs(nutrition))})g carbs</div>

            <div>
                <meter max={min_protein} value={nutrition.protein}/>
                {formatNumber(min_protein)}
                <meter min={min_protein} max={max_protein} value={nutrition.protein}/>
                {formatNumber(max_protein)}
            </div>
            <div>{formatNumber(nutrition.protein)}g protein</div>

            <button onClick={() => reset()}>reset</button>
            <button onClick={() => nextDay()}>new day</button>

            <h1>Menu</h1>

            <table>
                <thead>
                <tr>
                    <td></td>
                    <td>g</td>
                    <td>ml</td>
                    <td>name</td>
                    <td>calories</td>
                    <td>fat (g)</td>
                    <td>carbs (g)</td>
                    <td>protein (g)</td>
                </tr>
                </thead>
                <tbody>
                {foods.map(it => <tr>
                    <td>
                        <button onClick={() => addCount(it)}>+</button>
                        {it.g ? <button onClick={() => addGrams(it)}>+g</button> : undefined}
                        {it.ml ? <button onClick={() => addMilliliters(it)}>+ml</button> : undefined}
                    </td>
                    <td>{formatNumber(it.g)}</td>
                    <td>{formatNumber(it.ml)}</td>
                    <td>{it.name}</td>
                    <td>{formatNumber(it.calories)}</td>
                    <td>{formatNumber(it.fat)}</td>
                    <td>{formatNumber(it.carbs)}{formatNumber(netCarbs(it)) !== formatNumber(it.carbs) ? (" ("+formatNumber(netCarbs(it))+")") : undefined}</td>
                    <td>{formatNumber(it.protein)}</td>
                </tr>)}
                </tbody>
            </table>

            <h1>Meals</h1>

            <table>
                <thead>
                <tr>
                    <td></td>
                    <td>servings</td>
                    <td>name</td>
                    <td>calories</td>
                    <td>fat (g)</td>
                    <td>net carbs (g)</td>
                    <td>protein (g)</td>
                </tr>
                </thead>
                <tbody>
                {meals.map((it, i) => <tr>
                    <td>
                        <button onClick={() => remove(i)}>-</button>
                    </td>
                    <td></td>
                    <td>{it.name}</td>
                    <td>{formatNumber(it.calories)}</td>
                    <td>{formatNumber(it.fat)}</td>
                    <td>{formatNumber(it.carbs)}{formatNumber(netCarbs(it)) !== formatNumber(it.carbs) ? (" ("+formatNumber(netCarbs(it))+")") : undefined}</td>
                    <td>{formatNumber(it.protein)}</td>
                </tr>)}
                </tbody>
            </table>


            {/*<div>{JSON.stringify(meals)}</div>*/}
        </div>
    );
}

export default App;
